.cards {
    padding: 4rem;
    background: rgb(255, 255, 255);
    padding-top: 80px;
  }
  
  h1 {
    text-align: center;
    z-index: 0;
  }
  
  .cards_container {
    display: flex;
    flex-flow: column;
    /*align-items: center;*/
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
  }
  
  .cards_wrapper {
    position: relative;
    margin: 50px 0 45px;
  }
  
  .cards_entries {
    margin-bottom: 24px;
  }
  
  .cards_entry {
    display: flex;
    flex: 1;
    margin: 0 1rem;
    border-radius: 10px;
    width: 100%;
  }
  
  .cards_entry_link {
    display: flex;
    flex-flow: column;
    width: 100%;
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
  }
  
  .cards_entry_pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
  }
  
  .fade-img {
    animation-name: fade-img;
    animation-duration: 2s;
  }
  
  .cards_entry_pic-wrap::after {
    content: attr(content-label);
    position: absolute;
    bottom: 0;
    margin-left: 10px;
    padding: 6px 8px;
    max-width: calc((100%) - 60px);
    font-size: 12px;
    font-weight: normal;
    color: #fff;
    background-color: #1f98f4;
    box-sizing: border-box;
  }
  
  .cards_entry_img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
  }
  
  .cards_entry_img:hover {
    transform: scale(1.1);
  }
  
  .cards_entry_info {
    padding: 20px 30px 30px;
  }
  
  .cards_entry_text {
    color: #252e48;
    font-size: 15px;
    line-height: 24px;
    font-weight: normal;
  }
  
  @media only screen and (min-width: 1024px) {
    .cards_entries {
      display: flex;
    }
  }
  
  @media only screen and (max-width: 1024px) {
    .cards_entry {
      margin-bottom: 2rem;
    }
    h1 {
      font-size: 25px;
    }
    .cards_entry {
      margin-right: 0;
      margin-left: 0;
      margin-top: 10px;
    }
  }